

.dashboard-container {
    margin-left: 240px;
    height: 100vh;
    width: calc(100vw - 240px);
    display: flex;
    flex-direction: row;
    overflow-y: auto;
    background-color: #F8F9FB;
    flex-grow: 1 I !important;
}



.sidebar {
    height: 100%;
    position: absolute;
    top: 50%;
    left: 0;
    width: 240px;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
  }  

.container {
    height: 100%;
    padding-left: 20px;
    width: calc(100vw - 240px);
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    overflow-x: hidden; 
}


@media screen and (max-width: 1600px) {
    .sidebar {
        width: 200px;
        
    }
    .dashboard-container {
        margin-left: 120px;
        height: 100vh;
        width: calc(100vw - 100px);
        display: flex;
        flex-direction: row;
        overflow-y: auto;
        background-color: #F8F9FB;
        overflow-x: hidden;

    }
    .container {
        height: 100%;
        padding-left: 20px;
        width: calc(100vw - 200px);
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        overflow-x: hidden; 
    }
}
