.AIbt{
    margin: 10px;
    cursor: pointer;
    padding: 10px 20px;
    border: none;
    background-color: #FFAFAF;
    border-radius: 12px;
    font-size: 24px;
    font-weight: bold;
    white-space: nowrap;
    color: white;
}

.AIBox{
    width: 100%;
    box-shadow: 1px 1px 5px grey;
    background-color: white;
    border-radius: 12px;
    margin: 30px 40px;
    padding: 30px 40px;
}

.AIText{
    font-size: 24px;
    margin-bottom: 24px;
}

.AISubText{
    font-size: 15px;
    margin-bottom: 36px;
}

.AIbts{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    margin-bottom: 50px;
}

.AIUpdate{
    display: flex;
    justify-content: end;
}

.HyAIbts{
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.AIText2{
    flex: 0 0 100px;
    width: 150px;
    font-size: 20px;
    margin-bottom: 24px;
    margin-right: 24px;
}

.AITextField{
    width: 350px;
    border: 2px solid rgb(180, 176, 176) !important;
    border-radius: 10px !important;
    margin-bottom: 20px;
    height: 50px;
    margin-top: 3px;
}

.ParameterContainer{
    display: flex;
  flex-direction: column;
}

.AIInputBox {
    display: flex;
    align-items: center; /* 수직 가운데 정렬 */
    margin-bottom: 10px; /* 각 열 사이의 간격 조정 */
  }


  .AItable {
    width: 84%;
    table-layout: fixed;
    border-collapse: collapse;
    font-size: 20px;
    color: #7A7171;
    font-weight: 500;
    margin: auto;
    height: 50px;
  }
  
  .AIth{
    font-size: 20px;
    padding: 25px;
    border-bottom: 1px solid black;
    text-align: center;
    white-space: nowrap;
    height: 50px;
    font-weight: 600;
    border-bottom: 1px solid #F5F7FA;
    
  }
  
  .AItd {
    padding: 25px;
    border-bottom: 1px solid #ececee;
    text-align: center;
    white-space: nowrap;
    height: 50px;
    font-size: 15px;
    font-weight: 600;
  }
  
  .AIth:nth-child(1),
  .AItd:nth-child(1) {
    width: 20%;
  }
  
  .AIth:nth-child(2),
  .AItd:nth-child(2) {
    width: 20%;
  }
  
  .AIth:nth-child(3),
  .AItd:nth-child(3) {
    width: 15%;
  }
  
  .AIth:nth-child(4),
  .AItd:nth-child(4) {
    width: 15%;
  }
  
  .AIth:nth-child(5),
  .AItd:nth-child(5) {
    width: 15%;
  }

  .versionSelectText{
    font-size: 24px;
    font-weight: bold;
    margin: 30px 100px;
  }

  .versionBts{
    margin: 30px 40px;
    display: flex;
    justify-content: end;
  }