.menu{
    align-items: center;
    flex-direction: column;
    display: flex;
    background-color: #1B1D2A;
    width: 240px;
    height: 100%;
    overflow-y: hidden;
    align-items: center;
}

.button-container {
    display: flex;
    align-items: center;
}



.menu h1 {
    font-family: "Kaushan Script";
    font-size: 25px;
    margin-left: 20px;
    margin-right: 20px;
    margin-bottom: 35px;
    color: #FFFFFF;
    white-space: nowrap;
    font-weight: 100;
    margin-bottom: 60px;
}

.menu button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 15px;
    font-weight: bold;
    width: 220px;
    height: 60px;
    box-sizing: border-box;
    padding: 10px;
