/* Table */
.usertable {
  width: 84%;
  table-layout: fixed;
  border-collapse: collapse;
  font-size: 20px;
  color: #7A7171;
  font-weight: 500;
  margin: auto;
  height: 50px;
}

.userth{
  font-size: 20px;
  padding: 25px;
  border-bottom: 1px solid black;
  text-align: center;
  white-space: nowrap;
  height: 50px;
  font-weight: 600;
  border-bottom: 1px solid #F5F7FA;
  
}

.usertd {
  padding: 25px;
  border-bottom: 1px solid #ececee;
  text-align: center;
  white-space: nowrap;
  height: 50px;
  font-size: 15px;
  font-weight: 600;
  padding-top: 0;
  padding-bottom: 0;
}

.userth:nth-child(1),
.usertd:nth-child(1) {
  width: 10%;
}

.userth:nth-child(2),
.usertd:nth-child(2) {
  width: 10%;
}

.userth:nth-child(3),
.usertd:nth-child(3) {
  width: 10%;
}

.userth:nth-child(4),
.usertd:nth-child(4) {
  width: 20%;
}

.userth:nth-child(5),
.usertd:nth-child(5) {
  width: 10%;
}

.userth:nth-child(6),
.usertd:nth-child(6) {
  width: 15%;
}

