/* numContainer */


@media screen and (min-width: 1600px) {

    .chart{
        margin-right: 80px;
    }

    .chart1{
        height: 260px;
        width: 500px;  
    }
    
    .chart2{
    
        height: 260px;
        width: 500px;
    
    }
    .name hr {
        width: calc(100% - 400px); 
    }

}

@media screen and (max-width: 1599px) {

    .chart{
        margin-right: 0px;
    }
    .chart1{
        height: 200px;
        width: 400px;

    }
    
    .chart2{
    
        height: 200px;
        width: 400px;
    
    }
    .name hr {
        width: calc(100% - 300px); 
    }

}


.text{
    font-size: 15px;
}

.image {
    width: 80px; 
    height: 80px; 
    text-align: center;
    background-color: #FFE3E3;
    border-radius: 35px;
    display: flex; 
    justify-content: center; 
    align-items: center; 
}


.num{
    justify-content: center;
    font-size: 25px;
}


.Total {
    display: flex;
    margin-left: auto; 
    margin-right: auto; 
    max-width: 1900px; 
}

.numcontainer{
    width: 300px;
    margin-right: 50px;
    padding: 10px;
    text-align: center;
    border-radius: 10px;
    height: 100px;
    background-color: white;
    font-weight: bold;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center; 
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
    
}

.numContainer{
    display: flex;
}

/* chart */
.chartgraph{
    align-items: center;
    display: flex;
    width: 80%;
    margin-bottom: 100px;
    flex-grow: 1 I !important;

}

.chart{
    font-family: 'DM Sans', sans-serif;
}
.chart1{
    font-size: 20px;
    color: gray;
    font-weight: bold;

    display: flex; 
    justify-content: center; 
    align-items: center; 
    padding-top: 60px;
    padding-right: 15px;
    margin-right: 100px;
    margin-left: 80px;
    margin-bottom: 100px;
    
}

.chart2{

    color: gray;
    font-weight: bold;
    display: flex; 
    justify-content: center; 
    align-items: center; 
    margin-left: 40px;
    padding-top: 60px;
    padding-right: 15px;
    margin-right: 100px;
    margin-left: 90px;

}
.evaluation{
    align-items: center;
    display: flex;
}

.evaluation1{
    width: 600px;
    height: 350px;
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
    display: flex; 
    justify-content: center; 
    align-items: center; 

    
}
.evaluation2{
    display: flex; 
    justify-content: center; 
    align-items: center; 
    background-color: white;
    border-radius: 10px;
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
    margin-bottom: 50px;
}
.title{
    margin-top: 80px;
    font-size: 40px;
    font-weight: 500;
}
.title2{
    margin-bottom: 50px;
}

hr{
    width: 2000px;
    border: 1px solid rgba(128, 128, 128, 0.1);
    margin-bottom: 30px;
}

.name hr {
    border: 1.4px solid black;
    padding: 0;
    margin-right: 400px;
 }

.part1{
    margin-left: 100px;
}

.part2{
    width: 100vw;
 

}

.name{
    margin-left: 90px;
    font-size: 20px;
    font-weight: 600;
}

.name2{
    margin-top: 50px;
    margin-left: 250px;
    font-size: 20px;
    font-weight: 600;
}

.name3{
    margin-top: 120px;
    margin-left: 250px;
    font-size: 20px;
    font-weight: 600;
}

@media screen and (max-width: 1600px) {

    .evaluation1{
        width: 600px;
        height: 350px;
        background-color: white;
        border-radius: 10px;
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
        display: flex; 

        margin-bottom: 100px;
        
    }
    .evaluation2{
        display: flex; 
        justify-content: center; 
        align-items: center; 
        background-color: white;
        border-radius: 10px;
        box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.2);
        margin-bottom: 50px;
    }
    .chart{
        font-family: 'DM Sans', sans-serif;
        margin-right: 10px;
    }

}