.registerTable {
  width: 95%;
  table-layout: fixed;
  border-collapse: collapse;
  font-size: 20px;
  color: #7A7171;
  font-weight: 500;
  margin: auto;
  height: 50px;
  }
  
  .registerth{
    font-size: 20px;
    padding: 25px;
    border-bottom: 1px solid black;
    text-align: center;
    white-space: nowrap;
    height: 40px;
    font-weight: 600;
    border-bottom: 1px solid #F5F7FA;
    word-wrap: break-word;
  }


  .registertd_bts_box{
    display: grid;
  }

  .registertd {
    padding: 25px;
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #ececee;
    text-align: center;
    height: 50px;
    font-size: 15px;
    font-weight: 600;
    white-space: normal;
  }
  
  .registerth:nth-child(1),
  .registertd:nth-child(1) {
    width: 10%;
  }
  
  .registerth:nth-child(2),
  .registertd:nth-child(2) {
    width: 10%;
  }
  
  .registerth:nth-child(3),
  .registertd:nth-child(3) {
    width: 10%;
  }
  
  .registerth:nth-child(4),
  .registertd:nth-child(4) {
    width: 10%;
  }
  
  .registerth:nth-child(5),
  .registertd:nth-child(5) {
    width: 20%;
    overflow-wrap: break-word;
  }
  
  .registerth:nth-child(6),
  .registertd:nth-child(6) {
    width: 5%;
  }

  .registerth:nth-child(7),
  .registertd:nth-child(7) {
    width: 10%;
  }
  
  .registertr {
    font-size: 18px;
    color: black;
    font-weight: 500;
  }
  
