.Link_Date{
  font-size: 15px;
  text-align: center;
  width: 300px;
  margin-top: 20px;
}

.Link_Input_Box{
  margin-left: 80px;
  margin-top: 100px;
}



.linktable {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  font-size: 15px;
  color: #7A7171;
  font-weight: 500;

}
.linktableBoxOut{

}

.linkth{
  padding: 30px;

  border-bottom: 1px solid black;
  text-align: center;
  white-space: nowrap;
  border-bottom: 1px solid #ececee;
  white-space: normal;
}
.linktd {
  padding: 30px;
  padding-top: 10px;
  padding-bottom: 10px;
  border-bottom: 1px solid black;
  text-align: center;
  white-space: nowrap;
  border-bottom: 1px solid #ececee;
  white-space: normal;
}

.linkth:nth-child(1),
.linktd:nth-child(1) {
  width: 20%;
}

.linkth:nth-child(2),
.linktd:nth-child(2) {
  width: 15%;
}

.linkth:nth-child(3),
.linktd:nth-child(3) {
  width: 25%;
}

.linkth:nth-child(4),
.linktd:nth-child(4) {
  width: 15%;
}

.columns {
  font-size: 18px;
  color: black;
  font-weight: 500;
}



/* Search */
.search {
  border-radius: 10px;
  margin-right: 10px;
  font-size: 20px;
  width: 250px;
  height: 35px;
}

.search-icon {
  font-size: 25px;
  margin-right: 10px;
}

.search-conatiner {
  display: flex;
  justify-content: start;
  align-items: center;
}


/* Button */
.bt {
  margin: 10px;
  cursor: pointer;
  padding-bottom: 10px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  border: none;
  background-color: #f17a7a;
  border-radius: 12px;
  font-size: 18px;
  font-weight: 600;
}

.cImage {
  width: 100px;
  height: 100px;
}

/* 관리 */
.link-container{
  display: flex;
  flex-direction: column;
  margin-right: 50px;
  width: 300px;
}

.cosmetic-name{
  font-size: 20px;
  margin-top: 30px;
  margin-bottom: 30px;
  width: 250px;
  padding: 6px !important;
  padding-top: 10px !important;
  padding-bottom: 10px !important;
  border-radius: 10px !important;
  border: 2px solid rgb(185, 185, 185) !important;
}
.LLLL{
  margin-bottom: 100px;
}

.total-link-container{
  top: 50%;
  left: 6%;
  padding-top: 0 !important;
  position: relative;
  display: flex;

  justify-content: center;
  white-space: nowrap;
    background-color: white;
    box-shadow: 1px 1px 1px 1px rgb(204, 204, 204);
    z-index: 1;
    width: 1200px;
    height: 900px;
    border-style: solid;
    border-width: 1px;
    border-color: #e8ecf6;
    background-color: #fff;
}

.purchase-container {
  border-bottom: 2px black;
  border-top: 2px black;
  display: flex;
  align-items: center;
}

.purchaseSite {
  margin-right: 40px;
  width: 100px;
  height: 30px;
  font-size: 24px;
}

.link_bts_box{
  margin-left: 290px;
}

.linktextfield {
  padding: 6px;
  width: 400px;
  height: 30px;
  font-size: 24px;
}

.linklabel {
  font-size: 24px;
  margin-right: 20px;
}

.infoImage{
  margin-top: 100px;
  width: 300px;
  height: 400px;
}