  .ingredientth{
    padding: 30px;
    text-align: center;
    word-wrap: break-word;
    border-bottom: none;
    border-bottom: 1px solid #ececee;
  }
  .ingredienttd {
    padding: 8px;
    text-align: center;
    word-wrap: break-word;
    border-bottom: none;
    border-bottom: 1px solid #ececee;
    font-size: 15px;
  }

  .ingredientth {
    border-bottom: 1px solid #ececee;
  }
  
  .ingredientth:nth-child(1),
  .ingredienttd:nth-child(1) {
    width: 40%;
  }
  
  .ingredientth:nth-child(2),
  .ingredienttd:nth-child(2) {
    width: 15%;
  }
  
  .ingredientth:nth-child(3),
  .ingredienttd:nth-child(3) {
    width: 15%;
  }
  
  .ingredientth:nth-child(4),
  .ingredienttd:nth-child(4) {
    width: 25%;
  }
  
  .ingredientth:nth-child(5),
  .ingredienttd:nth-child(5) {
    width: 20%;
  }

  
  .columns {
    font-size: 18px;
    color: #707070;
    font-weight: 500;
  }
  
  
  
  .bt {
    margin: 10px;
    cursor: pointer;
    padding-bottom: 10px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    border: none;
    background-color: #ff8d8d;
    border-radius: 12px;
    font-size: 15px;
    font-weight: 500;
  }

  
  .bt_box{
    margin-top: 50px;
    margin-left:1000px;
  }

  /* Info */
  .ingredientTextField{
    margin-bottom: 30px;
    padding: 12px;
    font-size: 20px;
    border-radius: 12px;
    border: 2px solid grey;
  }

